.card{
    display: flex;
    margin-bottom: 1vw;
    margin-top: 1vw;
    border: dotted 2px #ccc;
}

.title {
    margin-top: auto;
    margin-bottom: auto;
    display: block;
    margin-left: 2vw;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 600;
    font-size: 1.25vw;
    word-wrap: normal;

}

.head_title {
    text-align: center;
    font-size: 5vw;
}

.wrapper{
    margin-left: 10vw;
    margin-right: 10vw;
}

.image {
    width: 15vw ;
    height: 8vw ;
}

@media only screen and (max-width: 800px) {
    .card{
        flex-direction: column;
        padding: 1vw;
    }
    
    .title {
        text-align: center;
        display: block;
        margin-left: 0;
        margin-top: 1vw;
        font-size: 2.5vw;
    }
    
    .head_title {
        text-align: center;
        font-size: 6vw;
    }
    
    .image {
        width: 30vw ;
        height: 15vw ;
        display: block;
        margin-right: auto;
        margin-left: auto;
    }
}